<template>
  <div>
    <Club
      ref="club"
      @register="donorId = $event"
      :id="donorId"
      v-if="page === 'club'"
    />
    <Crimson v-if="page === 'crimson'" />
    <Article v-if="page === 'article'" />
    <SportStar v-if="page === 'sports'" />
    <Donor @checkDonor="checkDonor" v-if="page === 'donor'" />
    <SportStarManageMent v-if="page === 'sportsManage'" />
    <ArticleManageMent v-if="page === 'articleManage'" />

    <Login v-if="page === 'login'" @login="$emit('login')" />
  </div>
</template>
<script>
import Club from '../view/Club.vue'
import Crimson from '../view/Crimson.vue'
import SportStar from '../view/SportStar.vue'
import Article from '../view/Article.vue'
import Donor from '../view/Donor.vue'
import Login from '../view/Login.vue'
import ArticleManageMent from '../view/ArticleManagement.vue'
import SportStarManageMent from '../view/SportStarManagement.vue'

export default {
  props: ['page'],
  mounted() {
    console.log(this.page)
  },
  data() {
    return {
      donorId: -1,
    }
  },
  components: {
    Crimson,
    SportStar,
    Article,
    Club,
    Donor,
    Login,
    ArticleManageMent,
    SportStarManageMent,
  },
  methods: {
    checkDonor(id) {
      this.donorId = id
      this.$nextTick(() => {
        this.$emit('show', 'club')
      })
    },
  },
}
</script>

