<template>
  <div>
    <div>
      <div style="display: flex; justify-content: space-between">
        <h2 style="color: #900d33">Donor 관리 (수정/삭제)</h2>
        <img @click="init" src="../assets/back.svg" alt="back" />
      </div>
      <h2>기부자 프로필을 수정/삭제 관리하는 페이지입니다.</h2>
      <search-donor
        :keyword="keyword"
        @search="onSearch"
        @onKeyUp="onChangeKeyword"
      />
      <div
        style="
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-top: 36px;
        "
      >
        <button @click="onDelete" style="font-size: 20px" class="donor-btn">
          삭제
        </button>
        <div>
          검색결과
          <span class="search-count">{{ count }}개</span>
        </div>
      </div>
      <table>
        <thead>
          <th width="10%">선택</th>
          <th width="50%">이름</th>
          <th width="10%">클럽명</th>
          <th width="10%">인명구분/직책</th>
          <th width="10%">업로드 일자</th>
          <th width="10%">관리</th>
        </thead>
        <tbody>
          <tr v-for="(row, idx) in searchResult" :key="idx">
            <td
              style="
                display: flex;
                align-items: center;
                justify-content: center;
              "
            >
              <input
                @change="onCheck($event, row.id)"
                type="checkbox"
                :name="idx"
                :id="idx"
              />
              <label class="checkbox-label" :for="idx"></label>
            </td>
            <td
              style="
                max-width: 500px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                text-align: start;
              "
            >
              {{ row.name }}
            </td>
            <td>
              {{ clubIdToString(row.club) }}
            </td>
            <td>
              {{ row.role }}
            </td>
            <td>
              {{ row.createdAt.split('T')[0].replaceAll('-', '.') }}
            </td>
            <td>
              <button
                style="font-size: 1rem; white-space: nowrap"
                class="donor-btn"
                @click="checkDonor(row.id)"
              >
                수정
              </button>
            </td>
          </tr>
        </tbody>
      </table>
      <div style="display: flex; width: 100%; justify-content: center">
        <img
          @click="onChangePageOffset('left')"
          src="../assets/leftArrow.svg"
          alt="left"
          style="cursor: pointer"
          class="mr-12"
        />
        <div
          v-if="parseInt(parseInt(this.count / 10) / 5) !== this.offset"
          style="display: flex"
        >
          <div
            v-for="idx in 5"
            :key="'page-' + idx"
            style="padding: 5px; font-size: 20px; display: flex"
            @click="onPagination(idx - 1)"
          >
            {{ idx + offset * 5 }}
          </div>
        </div>
        <div v-else style="display: flex">
          <div
            v-for="idx in parseInt(parseInt(count - offset * 50) / 10) + 1"
            :key="'page-' + idx"
            style="padding: 5px; font-size: 20px"
            @click="onPagination(idx - 1)"
          >
            {{ idx + offset * 5 }}
          </div>
        </div>

        <img
          @click="onChangePageOffset('right')"
          src="../assets/rightArrow.svg"
          alt="left"
          style="cursor: pointer"
          class="ml-12"
        />
      </div>
    </div>
  </div>
</template>
<script>
import SearchDonor from '../components/SearchDonor.vue'
import api from '../api'

export default {
  components: { SearchDonor },
  data() {
    return {
      page: 0,
      petPage: 10,
      count: 0,
      offset: 0,
      searchResult: [],
      deleteList: [],
      keyword: '',
    }
  },

  async mounted() {
    this.init()
  },
  methods: {
    onChangeKeyword(event) {
      const { value } = event
      console.log(value)
      this.keyword = value
    },
    onChangePageOffset(dir) {
      if (dir === 'left') {
        if (this.offset === 0) {
          return
        } else {
          this.offset -= 1
        }
      } else {
        if (parseInt(parseInt(this.count / 10) / 5) === this.offset) {
          return
        } else {
          this.offset += 1
        }
      }
    },
    async init() {
      const res = await api.getDonor({ page: this.page })
      this.keyword = ''
      this.searchResult = res.data.rows
      this.count = res.data.count
    },
    async onPagination(page) {
      this.page = page
      this.onSearch()
    },
    async onSearch() {
      const res = await api.getDonor({ page: this.page, keyword: this.keyword })
      this.searchResult = res.data.rows
      this.page = 0
      this.offset = 0
      this.count = res.data.count
    },
    onCheck(event, id) {
      if (event.target.checked) {
        this.deleteList.push(id)
      } else {
        this.deleteList = this.deleteList.filter((exId) => exId !== id)
      }
    },
    checkDonor(id) {
      this.$emit('checkDonor', id)
    },
    async onDelete() {
      const res = await api.deleteDonors({
        deleteList: this.deleteList,
      })
      if (res.success) {
        window.alert('기부자가 성공적으로 삭제되었습니다.')
        this.deleteList = []
        window.location.reload()
      }
    },
    clubIdToString(id) {
      switch (id) {
        case 1:
          return 'Presidents'

        case 2:
          return 'Platinum'

        case 3:
          return 'gold'

        case 4:
          return 'roseGold'

        case 5:
          return 'whiteGold'
        case 6:
          return 'silver'
        case 7:
          return 'partner'
        case 8:
          return 'default'
      }
    },
  },
}
</script>
