import axios from "axios"
import qs from "querystring"

// const BASE_URL = "http://localhost:8000/admin/";
const BASE_URL = "./admin/";

async function _baseGetRequest(url) {
    try {

        const { data } = await axios.get(url)
        return data
    } catch (e) {
        console.log(e)
        return e
    }
}

async function _basePostRequest(url, params) {
    try {

        const { data } = await axios.post(url, params)
        return data
    } catch (e) {
        console.log(e)
        return e
    }
}
async function _basePutRequest(url, params) {
    try {

        const { data } = await axios.put(url, params)
        return data
    } catch (e) {
        console.log(e)
        return e
    }
}

async function _baseDeleteRequest(url, params) {
    try {

        const { data } = await axios.delete(url, {
            data: params
        })
        return data
    } catch (e) {
        console.log(e)
        return e
    }
}

const searchDonor = (params) => {
    const res = _basePostRequest(BASE_URL + "article/check", params)
    return res
}

const postArticle = (params) => {
    const res = _basePostRequest(BASE_URL + "article", params)
    return res;
}

const postSportStar = (params) => {
    const res = _basePostRequest(BASE_URL + 'sports', params)
    return res
}

const postCrimson = (params) => {
    const res = _basePostRequest(BASE_URL + "crimson", params)
    return res
}

const postDonor = (params) => {
    const res = _basePostRequest(BASE_URL + "donor", params)
    return res;
}

const getDonor = (query) => {
    const res = _baseGetRequest(BASE_URL + 'donor?' + qs.stringify(query))
    return res;
}

const postDonorv2 = (params) => {
    const res = _basePostRequest(BASE_URL + "preDonor", params)
    return res;
}

const getDonorDetail = (id) => {
    const res = _baseGetRequest(`${BASE_URL}donor/${id}`)
    return res;
}

const putDonor = (params, id) => {
    const res = _basePutRequest(`${BASE_URL}donor/${id}`, params)
    return res;
}

const postDonorImage = (params, id) => {
    const res = _basePostRequest(`${BASE_URL}donor/${id}/image`, params)
    return res;
}

const deleteDonors = (params) => {
    const res = _baseDeleteRequest(BASE_URL + 'donor', params)
    return res;
}

const login = (params) => {
    const res = _basePostRequest(BASE_URL + 'login', params)
    return res;
}

// 기사 관리
const getArticle = (query) => {
    const res = _baseGetRequest(BASE_URL + 'article?' + qs.stringify(query))
    return res;
}

const deleteArticles = (params) => {
    const res = _baseDeleteRequest(BASE_URL + 'article', params)
    return res;
}

// 스포츠 스타 관리
const getStar = (query) => {
    const res = _baseGetRequest(BASE_URL + 'sportstar?' + qs.stringify(query))
    return res;
}

const deleteStars = (params) => {
    const res = _baseDeleteRequest(BASE_URL + 'sportstar', params)
    return res;
}

export default {
    searchDonor,
    postArticle,
    postSportStar,
    postCrimson,
    postDonor,
    postDonorv2,
    postDonorImage,
    putDonor,
    getDonor,
    getDonorDetail,
    deleteDonors,
    login,
    // 기사 관리
    getArticle,
    deleteArticles,
    // 스포츠 스타 관리
    getStar,
    deleteStars

}
