<template>
  <div>
    <h2 class="page-info">
      1_Donor Profile
      <span
        style="
          font-size: 24px;
          line-height: 35px;

          color: #000000;
        "
        >Guide : 1단계(기부자 정보) 완료 후, 2단계 (기부자 사진)
        완료해주세요.</span
      >
    </h2>
    <div>
      <Select
        :label="true"
        labelHolder="클럽명"
        name="clubId"
        :items="items"
        style="margin-bottom: 75px"
        v-model="clubData.clubId"
        @onChange="onChange"
      />
      <Input
        :label="true"
        labelHolder="기부자 이름"
        name="name"
        :disabled="id !== -1"
        :value="clubData.name"
        placeholder="텍스트입력"
        style="margin-bottom: 75px"
        @onKeyUp="onChange"
      />
      <Input
        :label="true"
        labelHolder="인명구분(기업 / 재단 / 개인)"
        :value="clubData.role"
        name="role"
        placeholder="텍스트입력"
        style="margin-bottom: 75px"
        @onKeyUp="onChange"
      />
      <Input
        :label="true"
        labelHolder="소속"
        :value="clubData.belong"
        name="belong"
        placeholder="텍스트입력"
        style="margin-bottom: 75px"
        @onKeyUp="onChange"
      />
      <Input
        :label="true"
        labelHolder="직책"
        :value="clubData.position"
        name="position"
        placeholder="텍스트입력"
        style="margin-bottom: 75px"
        @onKeyUp="onChange"
      />
      <TextArea
        v-if="clubData.clubId < 7"
        name="description"
        :value="clubData.description"
        minHeight="300px"
        :label="true"
        labelHolder="기부자 소개"
        style="margin-bottom: 50px"
        @onKeyUp="onChange"
      />
      <div
        style="
          display: flex;
          justify-content: center;
          padding-bottom: 50px;
          border-bottom: 1px solid #c4c4c4;
        "
      >
        <button
          @click="onSubmitv2"
          style="margin: 0 auto"
          class="btn btn-secondary"
        >
          1단계 등록
        </button>
      </div>
    </div>

    <div v-if="isRegistered && this.id !== -1 && clubData.clubId < 7">
      <h2 class="page-second">2단계 - 기부자 기사 및 사진 등록</h2>
      <ImageInput
        v-if="clubData.clubId < 7"
        name="thumb"
        :label="true"
        labelHolder="기부자 대표 이미지"
        @onChange="onChangeThumb"
        style="margin-bottom: 75px"
        info="대표이미지 프레임 1100x1100px"
      />
      <ImageInput
        v-if="clubData.clubId < 4"
        name="logo"
        :label="true"
        labelHolder="기업 BI / CI / Logo 이미지 업로드"
        info="최대 300x300px"
        @onChange="onChangeLogo"
        style="margin-bottom: 75px"
      />
      <MultyImageInput
        v-if="clubData.clubId < 7"
        name="thumb_detail"
        :label="true"
        labelHolder="기부자 추가 이미지"
        @onChange="onMultyImageChange"
        @remove="onDeleteMultyImageFile"
        style="margin-bottom: 75px"
        info="최소 250x250px  최대 1024x1024"
      />
      <div style="display: flex; justify-content: center; padding-bottom: 50px">
        <button @click="onSubmitImage" class="btn btn-danger">최종 저장</button>
      </div>
    </div>

    <!-- <ButtonGroup @ok="onSubmit" /> -->
  </div>
</template>
<script>
import Select from '../components/common/Select.vue'
import Input from '../components/common/Input.vue'
import TextArea from '../components/common/TextArea.vue'
import ImageInput from '../components/common/ImaegInput.vue'
import MultyImageInput from '../components/common/MultyImageInput.vue'
import Compressor from 'compressorjs'
// import ButtonGroup from '../components/ButtonGroup.vue'

import api from '../api'

export default {
  props: ['id'],
  components: {
    Select,
    Input,
    TextArea,
    ImageInput,
    MultyImageInput,
    // ButtonGroup,
  },
  data() {
    return {
      items: [
        { value: 1, name: 'President’s Honor Club ( 50억+ )' },
        { name: 'Platinum Honor Club ( 30억+ )', value: 2 },
        { value: 3, name: 'Gold Honor Club ( 10억+ )' },
        { value: 4, name: 'Rosegold Honor Club ( 5억+ )' },
        { value: 5, name: 'Whitegold Honor Club ( 3억+ )' },
        { value: 6, name: 'Silver Honor Club ( 1억+ )' },
        { value: 7, name: 'Partner Club ( 1천만원+ )' },
        { value: 8, name: '일반 기부자' },
      ],
      clubId: 10,
      clubData: {
        clubId: 10,
        name: '',
        belong: '',
        role: '',
        position: '',
      },
      thumb_detail: null,
      thumb: null,
      thumb_thumb_detail: [],
      logo: null,
      isRegistered: false,
    }
  },
  async mounted() {
    if (this.id !== -1) {
      const { data } = await api.getDonorDetail(this.id)
      this.isRegistered = true

      this.clubData = {
        clubId: data.club,
        name: data.name,
        belong: data.belong,
        role: data.role,
        position: data.position,
        description: data.description,
      }
    }
  },

  methods: {
    onChangeLogo(e) {
      this.logo = e.target.files[0]
    },
    onChangeThumb(e) {
      this.thumb = e.target.files[0]
    },
    onMultyImageChange(e) {
      this.thumb_detail = e.target.files
      let thumb_thumb_detail = []
      for (let i = 0; i < this.thumb_detail.length; i++) {
        if (this.thumb_detail[i] == null) {
          continue
        }

        new Compressor(this.thumb_detail[i], {
          quality: 0.8,
          convertTypes: 'image/png',
          success(result) {
            console.log(result)
            // The third parameter is required for server
            thumb_thumb_detail.push(result)
          },
        })
      }
      this.thumb_thumb_detail = thumb_thumb_detail
    },
    onDeleteMultyImageFile(idx) {
      const dataTransfer = new DataTransfer()

      let fileArray = Array.from(this.thumb_detail) //변수에 할당된 파일을 배열로 변환(FileList -> Array)
      fileArray.splice(idx, 1) //해당하는 index의 파일을 배열에서 제거
      fileArray.forEach((file) => {
        dataTransfer.items.add(file)
      }) //남은 배열을 dataTransfer로 처리(Array -> FileList)

      this.thumb_detail = dataTransfer.files
    },
    onChange(e) {
      const { name, value } = e
      if (name === 'clubId') {
        this.clubId = value
      }

      this.$set(this.clubData, name, value)
      console.log(this.clubData)
    },
    async onSubmitv2() {
      const sendData = {}
      if (this.clubData.clubId == 10) {
        window.alert('클럽을 선택해주세요')
        return
      }
      if (this.clubData.name == '') {
        window.alert('이름을 입력해주세요.')
        return
      }
      if (this.clubData.role == '') {
        window.alert('인명구분을 입력해주세요.')
        return
      }
      if (this.clubData.belong == '') {
        window.alert('소속을 입력해주세요.')
        return
      }
      if (this.clubData.position == '') {
        window.alert('직책을 입력해주세요.')
        return
      }

      sendData.clubId = this.clubData.clubId
      sendData.name = this.clubData.name
      sendData.role = this.clubData.role
      sendData.belong = this.clubData.belong
      sendData.position = this.clubData.position

      // 실버 이상 클럽들에 한함
      if (this.clubData.clubId < 7) {
        if (this.clubData.description == '') {
          window.alert('기부자 설명을 입력해주세요.')
          return
        }
        sendData.description = this.clubData.description
      }

      if (this.isRegistered && this.id !== -1) {
        const res = await api.putDonor(sendData, this.id)
        if (res.success) {
          window.alert('성공적으로 업데이트하였습니다.')
        } else {
          window.alert('시스템 에러입니다. 잠시후 다시 시도해주세요.')
        }
      } else {
        const res = await api.postDonorv2(sendData)
        if (res.success) {
          window.alert('성공적으로 업로드되었습니다.')
          this.$emit('register', res.data.id)
          this.isRegistered = true
        } else {
          if (res.errorCode === 'DP001') {
            window.alert('같은 클럽 내에 중복된 기부자가 존재합니다.')
          } else {
            window.alert('시스템 에러입니다. 잠시후 다시 시도해주세요.')
          }
        }
      }
    },
    async onSubmit() {
      const formData = new FormData()
      if (this.clubData.clubId == 10) {
        window.alert('클럽을 선택해주세요')
        return
      }
      if (this.clubData.name == '') {
        window.alert('이름을 입력해주세요.')
        return
      }
      if (this.clubData.role == '') {
        window.alert('인명구분을 입력해주세요.')
        return
      }
      if (this.clubData.belong == '') {
        window.alert('소속을 입력해주세요.')
        return
      }
      if (this.clubData.position == '') {
        window.alert('직책을 입력해주세요.')
        return
      }

      formData.append('clubId', this.clubData.clubId)
      formData.append('name', this.clubData.name)
      formData.append('role', this.clubData.role)
      formData.append('belong', this.clubData.belong)
      formData.append('position', this.clubData.position)

      // 실버 이상 클럽들에 한함
      if (this.clubData.clubId < 7) {
        if (this.clubData.description == '') {
          window.alert('기부자 설명을 입력해주세요.')
          return
        }

        if (this.thumb == null) {
          window.alert('대표사진을 입력해주세요.')
          return
        }
        if (this.thumb_detail == null) {
          window.alert('추가사진들을 입력해주세요.')
          return
        }
        formData.append('description', this.clubData.description)
        formData.append('thumb', this.thumb)
        for (let i = 0; i < this.thumb_detail.length; i++) {
          if (this.thumb_detail[i] == null) {
            continue
          }
          formData.append('thumb_detail', this.thumb_detail[i])
        }
      }

      // 골드 클럽 이상에 한함
      if (this.clubData.clubId < 4) {
        if (this.logo == null) {
          window.alert('로고를 입력해주세요.')
          return
        }
        formData.append('logo', this.logo)
      }

      const res = await api.postDonor(formData)
      if (res.success) {
        window.alert('성공적으로 업로드되었습니다.')
        window.location.reload()
      }
      console.log(res)
      console.log('gogo')
    },
    async onSubmitImage() {
      const formData = new FormData()

      // 실버 이상 클럽들에 한함
      if (this.clubData.clubId < 7) {
        if (this.thumb == null) {
          window.alert('대표사진을 입력해주세요.')
          return
        }
        if (this.thumb_detail == null) {
          window.alert('추가사진들을 입력해주세요.')
          return
        }

        formData.append('thumb', this.thumb)

        for (let i = 0; i < this.thumb_detail.length; i++) {
          if (this.thumb_detail[i] == null) {
            continue
          }
          formData.append('thumb_detail', this.thumb_detail[i])
          formData.append(
            'thumb_thumb_detail',
            this.thumb_thumb_detail[i],
            this.thumb_thumb_detail[i].name
          )
        }
      }

      // 골드 클럽 이상에 한함
      if (this.clubData.clubId < 4) {
        if (this.logo == null) {
          window.alert('로고를 입력해주세요.')
          return
        }
        formData.append('logo', this.logo)
      }
      console.log('통신 시작')

      const res = await api.postDonorImage(formData, this.id)
      if (res.success) {
        window.alert('성공적으로 업로드되었습니다.')
        console.log('통신 종료')
        window.location.reload()
      }
      console.log(res)
      console.log('gogo')
    },
  },
}
</script>
