<template>
  <div>
    <Input
      :label="true"
      labelHolder="클럽명"
      name="genre"
      :items="items"
      value="KU Sejong Crimson Club"
      :disabled="true"
      style="margin-bottom: 75px"
      @onKeyUp="onChange"
    />
    <Select
      :label="true"
      labelHolder="크림슨 등급"
      name="level"
      :items="items"
      style="margin-bottom: 75px"
      @onChange="onChange"
    />

    <Input
      :label="true"
      labelHolder="이름"
      name="name"
      placeholder="텍스트입력"
      style="margin-bottom: 75px"
      @onKeyUp="onChange"
    />
    <Input
      :label="true"
      labelHolder="주소"
      name="address"
      placeholder="텍스트입력"
      style="margin-bottom: 75px"
      @onKeyUp="onChange"
    />
    <Input
      :label="true"
      labelHolder="전화번호"
      name="phone"
      placeholder="텍스트입력"
      style="margin-bottom: 75px"
      @onKeyUp="onChange"
    />

    <ImageInput
      name="thumb"
      :label="true"
      labelHolder="대표이미지"
      @onChange="onChangeThumb"
      style="margin-bottom: 75px"
      info="대표이미지 프레임 960x960px 가로 세로 최대 1024px "
    />
    <MultyImageInput
      name="thumb_detail"
      :label="true"
      labelHolder="추가이미지"
      @onChange="onMultyImageChange"
      @remove="onDeleteMultyImageFile"
      style="margin-bottom: 75px"
      info="최대 280x280px 첫번째 사진은 등급 현판 이미지로 넣어주세요."
    />
    <ButtonGroup @ok="onSubmit" />
  </div>
</template>
<script>
import Input from '../components/common/Input.vue'
import Select from '../components/common/Select.vue'

import ImageInput from '../components/common/ImaegInput.vue'
import MultyImageInput from '../components/common/MultyImageInput.vue'
import ButtonGroup from '../components/ButtonGroup.vue'

import api from '../api'

export default {
  components: {
    Input,
    Select,
    ImageInput,
    MultyImageInput,
    ButtonGroup,
  },
  data() {
    return {
      thumb: null,
      thumb_detail: null,
      items: [
        { value: 'gold', name: 'gold' },
        { name: 'silver', value: 'silver' },
        { value: 'bronze', name: 'bronze' },
      ],
      crimsonData: {
        name: '',
        phone: '',
        address: '',
        level: '',
      },
    }
  },
  methods: {
    onChangeThumb(e) {
      this.thumb = e.target.files[0]
      console.log(this.thumb)
    },
    onChangeImage(e) {
      console.log(e)
      console.log(this.thumb_detail)
    },
    onMultyImageChange(e) {
      console.log(e.target.files)
      this.thumb_detail = e.target.files
      console.log(this.thumb_detail)
    },
    onDeleteMultyImageFile(idx) {
      const dataTransfer = new DataTransfer()

      let fileArray = Array.from(this.thumb_detail) //변수에 할당된 파일을 배열로 변환(FileList -> Array)
      fileArray.splice(idx, 1) //해당하는 index의 파일을 배열에서 제거
      fileArray.forEach((file) => {
        dataTransfer.items.add(file)
      }) //남은 배열을 dataTransfer로 처리(Array -> FileList)

      this.thumb_detail = dataTransfer.files
    },
    test() {
      console.log('dd')
    },
    onChange(e) {
      console.log(e)
      const { name, value } = e
      this.$set(this.crimsonData, name, value)
      console.log(this.crimsonData)
    },
    async onSubmit() {
      if (this.crimsonData.phone == '') {
        window.alert('연락처를 선택해주세요')
        return
      }
      if (this.crimsonData.name == '') {
        window.alert('이름을 입력해주세요.')
        return
      }
      if (this.crimsonData.address == '') {
        window.alert('주소를 입력해주세요.')
        return
      }
      if (this.crimsonData.level == '') {
        window.alert('크림슨 등급을 입력해주세요.')
        return
      }
      if (this.thumb == null) {
        window.alert('대표사진을 입력해주세요.')
        return
      }
      if (this.thumb_detail == null) {
        window.alert('추가사진들을 입력해주세요.')
        return
      }

      const formData = new FormData()
      formData.append('clubId', 9)
      formData.append('phone', this.crimsonData.phone)
      formData.append('name', this.crimsonData.name)
      formData.append('address', this.crimsonData.address)
      formData.append('level', this.crimsonData.level)
      formData.append('thumb', this.thumb)
      for (let i = 0; i < this.thumb_detail.length; i++) {
        if (this.thumb_detail[i] == null) {
          continue
        }
        formData.append('thumb_detail', this.thumb_detail[i])
      }

      const res = await api.postCrimson(formData)
      if (res.success) {
        window.alert('성공적으로 업로드되었습니다.')
        window.location.reload()
      }
      console.log(res)
      console.log('gogo')
    },
  },
}
</script>
