<template>
  <div :class="containerClasses">
    <p v-if="label" style="font-size: 28px">
      {{ labelHolder }}
    </p>
    <textarea
      :type="inputType"
      :disabled="disabled"
      :placeholder="placeholder"
      v-model="localValue"
      :class="classes"
      :style="style"
      :minHeight="minHeight"
    />
  </div>
</template>
<script>
import Vue from 'vue'

export default Vue.extend({
  name: 'bo-input',

  data() {
    return {
      inputType: 'text',
    }
  },
  props: {
    multiple: {
      type: Boolean,
    },
    type: {
      type: String,
      default: 'text',
    },
    minHeight: {
      type: String,
    },
    maxLength: {
      type: String,
    },
    name: {
      type: String,
      required: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    width: {
      type: String,
      default: 'full',
      validator: function (value) {
        return ['full', 'half', 'quarter'].indexOf(value) !== -1
      },
    },
    placeholder: {
      type: [String, Number],
    },
    value: {
      type: [String, Number],
    },
    active: {
      type: Boolean,
      default: false,
    },
    label: {
      type: Boolean,
      default: true,
    },
    labelHolder: {
      type: String,
    },
    height: {
      type: String,
      default: '40px',
    },
  },
  computed: {
    classes() {
      return {
        'bo-input': true,
        'bo-input--active': this.active,

        'bo-input--disabled': this.disabled,
      }
    },
    containerClasses() {
      return {
        [`bo-input--${this.width}`]: true,
      }
    },
    style() {
      return {
        height: this.minHeight,
      }
    },
    localValue: {
      get() {
        return this.value
      },
      set(newVal) {
        this.$emit('onKeyUp', { name: this.name, value: newVal })
      },
    },
  },
  mounted() {
    if (this.type === 'r-number') {
      this.inputType = 'text'
    } else {
      this.inputType = this.type
    }
  },
  methods: {
    onChange() {
      this.$emit('onChange')
    },
    onFocus() {
      this.$emit('onFocus')
    },
    onKeyup() {
      this.$emit('onKeyup')
    },
  },
})
</script>
<style scoped>
.bo-input--label {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 130%;
  /* or 36px */

  letter-spacing: 0.212772px;

  /* font/disable */

  color: #747474;
  margin-bottom: 20px;
}
.bo-input {
  padding: 12px 20px;

  background: #f6f6f6;
  border-radius: 17.5886px;
  width: 100%;

  /* text */
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 1rem;
  line-height: 1rem;
  color: #787878;
}
.bo-input--disabled {
  background: #eee;
}
.bo-input--full {
  width: 100%;
}
.bo-input--half {
  width: 50%;
}
.bo-input--quarter {
  width: 25%;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
}
</style>
