<template>
  <div :class="containerClasses">
    <p v-if="label" style="font-size: 28px">
      {{ labelHolder }}
      <span
        style="
          margin-left: 50px;
          font-family: 'Roboto';
          font-size: 24.6241px;
          letter-spacing: 0.212772px;

          color: #c4c4c4;
        "
      >
        {{ info }}</span
      >
    </p>
    <div style="display: flex; align-items: flex-end">
      <div class="input-box">
        {{ imageName.join(',') }}
      </div>
      <button @click="clickImageButton" class="input-button">Upload</button>
    </div>
    <input
      ref="imageInput"
      type="file"
      :disabled="disabled"
      :class="classes"
      :style="style"
      multiple
      @change="onFileChange"
    />
    <div style="display: flex; flex-wrap: wrap">
      <div
        v-for="(url, idx) in image"
        :key="idx"
        style="position: relative; margin-top: 80px; maring-right: 40px"
      >
        <img :src="url" class="image-preview" />
        <button class="image-preview-btn" @click="removeImage(idx)">
          <img src="../../assets/remove.svg" alt="remove" />
        </button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'bo-multy-image',

  data() {
    return {
      image: [],
      imageName: [],
    }
  },
  props: {
    multiple: {
      type: Boolean,
    },
    type: {
      type: String,
      default: 'text',
    },
    max: {
      type: String,
    },
    maxLength: {
      type: String,
    },
    name: {
      type: String,
      required: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    width: {
      type: String,
      default: 'full',
      validator: function (value) {
        return ['full', 'half', 'quarter'].indexOf(value) !== -1
      },
    },
    placeholder: {
      type: [String, Number],
    },
    value: {
      type: [String, Number],
    },
    active: {
      type: Boolean,
      default: false,
    },
    label: {
      type: Boolean,
      default: true,
    },
    labelHolder: {
      type: String,
    },
    info: {
      type: String,
    },
    height: {
      type: String,
      default: '40px',
    },
  },
  computed: {
    classes() {
      return {
        'bo-input': true,
        'bo-input--active': this.active,

        'bo-input--disabled': this.disabled,
      }
    },
    containerClasses() {
      return {
        [`bo-input--${this.width}`]: true,
      }
    },
    style() {
      return {
        height: '40px',
        visibility: 'hidden',
        display: 'none',
      }
    },
  },
  mounted() {
    if (this.type === 'r-number') {
      this.inputType = 'text'
    } else {
      this.inputType = this.type
    }
  },
  methods: {
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files
      if (!files.length) return

      for (let i = 0; i < files.length; i++) {
        console.log(files[i])
        this.createImage(files[i])
        this.imageName.push(files[i].name)
        // this.imageName = this.imageName + ',' + files[i].name
      }
      // this.imageName = this.imageName.replace(',', '')

      this.$emit('onChange', e)
    },
    createImage(file) {
      //   var image = new Image()

      var reader = new FileReader()
      var vm = this

      reader.onload = (e) => {
        // vm.image = e.target.result
        vm.image.push(e.target.result)
      }
      reader.readAsDataURL(file)
    },
    removeImage: function (idx) {
      this.image = this.image.filter((item, index) => index !== idx)
      this.imageName = this.imageName.filter((item, index) => index !== idx)
      this.removeFile(idx)
    },
    removeFile(idx) {
      this.$emit('remove', idx)
    },
    clickImageButton() {
      this.$refs.imageInput.click()
    },
  },
}
</script>
