<template>
  <div :class="containerClasses">
    <p v-if="label" style="font-size: 28px">
      {{ labelHolder }}
      <span
        style="
          margin-left: 50px;
          font-family: 'Roboto';
          font-size: 24.6241px;
          letter-spacing: 0.212772px;

          color: #c4c4c4;
        "
      >
        {{ info }}</span
      >
    </p>
    <div style="display: flex; align-items: flex-start">
      <div style="display: flex; align-items: flex-end; margin-right: 100px">
        <div class="input-box">
          {{ imageName }}
        </div>
        <button @click="clickImageButton" class="input-button">Upload</button>
      </div>
      <div
        v-if="image"
        style="
          position: relative;

          maring-right: 40px;
          text-align: center;

          width: 293px;
        "
      >
        <img :src="image" class="image-preview" />
        <p
          style="
            margin-top: 30px;

            font-weight: 400;
            font-size: 21.1063px;
            line-height: 130%;

            letter-spacing: 0.212772px;

            color: #989898;
          "
        >
          미리보기
        </p>
        <!-- <button class="image-preview-btn" @click="removeImage(idx)">
        <img src="../../assets/remove.svg" alt="remove" />
      </button> -->
      </div>
    </div>
    <input
      ref="imageInput"
      type="file"
      :disabled="disabled"
      :class="classes"
      :style="style"
      :multiple="multiple"
      @change="onFileChange"
    />
  </div>
</template>
<script>
export default {
  name: 'bo-input',

  data() {
    return {
      image: '',
      imageName: '',
    }
  },
  props: {
    multiple: {
      type: Boolean,
    },
    type: {
      type: String,
      default: 'text',
    },
    max: {
      type: String,
    },
    maxLength: {
      type: String,
    },
    name: {
      type: String,
      required: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    width: {
      type: String,
      default: 'full',
      validator: function (value) {
        return ['full', 'half', 'quarter'].indexOf(value) !== -1
      },
    },
    placeholder: {
      type: [String, Number],
    },
    value: {
      type: [String, Number],
    },
    active: {
      type: Boolean,
      default: false,
    },
    label: {
      type: Boolean,
      default: true,
    },
    labelHolder: {
      type: String,
    },
    info: {
      type: String,
    },
    height: {
      type: String,
      default: '40px',
    },
  },
  computed: {
    classes() {
      return {
        'bo-input': true,
        'bo-input--active': this.active,

        'bo-input--disabled': this.disabled,
      }
    },
    containerClasses() {
      return {
        [`bo-input--${this.width}`]: true,
      }
    },
    style() {
      return {
        height: '40px',
        visibility: 'hidden',
        display: 'none',
      }
    },
  },
  mounted() {
    if (this.type === 'r-number') {
      this.inputType = 'text'
    } else {
      this.inputType = this.type
    }
  },
  methods: {
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files
      if (!files.length) return
      this.createImage(files[0])
      this.imageName = files[0].name
      this.$emit('onChange', e)
    },
    createImage(file) {
      //   var image = new Image()
      var reader = new FileReader()
      var vm = this

      reader.onload = (e) => {
        vm.image = e.target.result
      }
      reader.readAsDataURL(file)
    },
    removeImage: function () {
      this.image = ''
    },
    clickImageButton() {
      this.$refs.imageInput.click()
    },
  },
}
</script>
