<template>
  <v-app class="app">
    <sidebar
      v-if="isLoggedIn"
      class="sidebar"
      @show="changePage($event, 'sidebar')"
    />
    <contents
      ref="content"
      class="content"
      :page="page"
      @show="changePage($event, 'content')"
      @login="login"
    />
  </v-app>
</template>

<script>
import Contents from './components/Contents.vue'

import Sidebar from './components/Sidebar.vue'

export default {
  name: 'App',

  components: {
    Contents,
    Sidebar,
  },
  data() {
    return {
      // page: null,
      page: 'login',
      isLoggedIn: false,
    }
  },
  watch: {
    $route(to, from) {
      console.lgo(to)
      if (to.path != from.path) {
        /* router path가 변경될 때마다 서버로 접근로그를 저장한다. */
      }
    },
  },
  mounted() {
    this.isLoggedIn = this.getCookie('historyroom')
    if (this.isLoggedIn) {
      this.page = 'donor'
    } else {
      this.page = 'login'
    }
  },

  methods: {
    changePage(e, from) {
      if (e === 'club' && from === 'sidebar') {
        this.$refs.content.donorId = -1
      }
      this.page = e
    },
    login() {
      this.isLoggedIn = true
      this.setCookie('historyroom', true, 1)
      this.page = 'donor'
    },
    setCookie(cookie_name, value, days) {
      var exdate = new Date()
      exdate.setDate(exdate.getDate() + days)
      // 설정 일수만큼 현재시간에 만료값으로 지정

      var cookie_value =
        escape(value) +
        (days == null ? '' : '; expires=' + exdate.toUTCString())
      document.cookie = cookie_name + '=' + cookie_value
    },
    getCookie(cookie_name) {
      var x, y
      var val = document.cookie.split(';')

      for (var i = 0; i < val.length; i++) {
        x = val[i].substr(0, val[i].indexOf('='))
        y = val[i].substr(val[i].indexOf('=') + 1)
        x = x.replace(/^\s+|\s+$/g, '') // 앞과 뒤의 공백 제거하기
        if (x == cookie_name) {
          return unescape(y) // unescape로 디코딩 후 값 리턴
        }
      }
    },
  },
}
</script>
