<template>
  <div style="display: flex; justify-content: flex-end; align-items: center">
    <p style="margin-right: 18px; margin-bottom: 0px">기부자명 검색</p>
    <input
      v-model="localValue"
      class="search"
      placeholder="기부자명을 입력해주세요"
      type="text"
      style="margin-right: 10px"
    />
    <button class="donor-btn" @click="$emit('search', keyword)">조회</button>
  </div>
</template>
<script>
export default {
  props: ['keyword'],

  computed: {
    localValue: {
      get() {
        return this.value
      },
      set(newVal) {
        this.$emit('onKeyUp', { value: newVal })
      },
    },
  },
}
</script>
<style lang="scss" scoped>
div,
span,
p {
  font-size: 15px;
}
.search {
  width: 273px;
  height: 35px;
  padding: 5px 10px;

  background: #f0f0f0;
  border-radius: 10px;
}
</style>
