<template>
  <div :class="containerClasses">
    <p v-if="label" style="font-size: 28px">{{ labelHolder }}</p>
    <div style="position: relative; max-width: 882px">
      <select
        :disabled="disabled"
        v-model="localValue"
        :class="classes"
        :style="style"
        @change="onChange"
      >
        <option v-for="(item, idx) in items" :value="item.value" :key="idx">
          {{ item.name }}
        </option>
      </select>
      <img
        class="select-dropdown"
        src="../../assets/dropdown.svg"
        alt="dropdown"
      />
    </div>
  </div>
</template>
<script>
export default {
  name: 'bo-select',

  data() {
    return {
      inputType: 'text',
    }
  },
  props: {
    items: {
      type: Array,
    },
    name: {
      type: String,
      required: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    width: {
      type: String,
      default: 'full',
      validator: function (value) {
        return ['full', 'half', 'quarter'].indexOf(value) !== -1
      },
    },
    placeholder: {
      type: [String, Number],
    },
    value: {
      type: [String, Number],
    },
    active: {
      type: Boolean,
      default: false,
    },
    label: {
      type: Boolean,
      default: true,
    },
    labelHolder: {
      type: String,
    },
    height: {
      type: String,
      default: '80px',
    },
  },
  computed: {
    classes() {
      return {
        'bo-input': true,
        'bo-input--active': this.active,

        'bo-input--disabled': this.disabled,
      }
    },
    containerClasses() {
      return {
        [`bo-input--${this.width}`]: true,
      }
    },
    style() {
      return {
        height: '80px',
      }
    },
    localValue: {
      get() {
        return this.value
      },
      set(newVal) {
        this.$emit('onKeyUp', { name: this.name, value: newVal })
      },
    },
  },
  mounted() {
    if (this.type === 'r-number') {
      this.inputType = 'text'
    } else {
      this.inputType = this.type
    }
  },
  methods: {
    onChange(e) {
      this.$emit('onChange', { name: this.name, value: e.target.value })
    },
    onFocus() {
      this.$emit('onFocus')
    },
    onKeyup() {
      this.$emit('onKeyup')
    },
  },
}
</script>
<style scoped>
.bo-input--label {
  font-family: Roboto;
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 130%;
  /* or 36px */

  letter-spacing: 0.212772px;

  /* font/disable */

  color: #747474;
  margin-bottom: 20px;
}
.bo-input {
  padding: 12px 20px;
  /* margin-top: 10px; */

  box-sizing: border-box;
  background: #f6f6f6;
  border-radius: 10px;
  width: 100%;

  height: 80px;

  /* text */
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 1rem;
  line-height: 1rem;
  color: #787878;
}
.bo-input--disabled {
  background: #eee;
}
.bo-input--full {
  width: 100%;
}
.bo-input--half {
  width: 50%;
}
.bo-input--quarter {
  width: 25%;
}
.select-dropdown {
  position: absolute;
  right: 34px;
  top: 50%;
  transform: translateY(-50%);
}
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
}
</style>
