<template>
  <div>
    <Select
      :label="true"
      labelHolder="종목"
      name="genre"
      :items="items"
      style="margin-bottom: 75px"
      @onChange="onChange"
    />

    <Input
      :label="true"
      labelHolder="이름"
      name="name"
      placeholder="텍스트입력"
      style="margin-bottom: 75px"
      @onKeyUp="onChange"
    />
    <Input
      :label="true"
      labelHolder="학적정보"
      name="major"
      placeholder="텍스트입력"
      style="margin-bottom: 75px"
      @onKeyUp="onChange"
    />
    <!-- <Input :label="true" labelHolder="대표이미지" type="file" name="thumb" /> -->
    <ImageInput
      name="thumb"
      :label="true"
      labelHolder="대표이미지"
      @onChange="onChangeThumb"
      style="margin-bottom: 75px"
      info="694 X 936 "
    />
    <MultyImageInput
      name="thumb_detail"
      :label="true"
      labelHolder="추가이미지"
      @onChange="onMultyImageChange"
      @remove="onDeleteMultyImageFile"
      style="margin-bottom: 75px"
    />

    <TextArea
      name="description"
      minHeight="300px"
      :label="true"
      labelHolder="스포츠 스타 소개"
      style="margin-bottom: 140px"
      @onKeyUp="onChange"
    />
    <ButtonGroup @ok="onSubmit" />
  </div>
</template>
<script>
import Select from '../components/common/Select.vue'
import Input from '../components/common/Input.vue'
import TextArea from '../components/common/TextArea.vue'
import ImageInput from '../components/common/ImaegInput.vue'
import MultyImageInput from '../components/common/MultyImageInput.vue'
import ButtonGroup from '../components/ButtonGroup.vue'

import api from '../api'

export default {
  components: {
    Select,
    Input,
    TextArea,
    ImageInput,
    MultyImageInput,
    ButtonGroup,
  },
  data() {
    return {
      items: [
        { name: '골프', value: '골프' },
        { name: '마라톤', value: '마라톤' },
        { name: '쇼트트랙', value: '쇼트트랙' },
        { name: '스키', value: '스키' },
        { name: '아티스틱스위밍', value: '아티스틱스위밍' },
        { name: '야구', value: '야구' },
        { name: '축구', value: '축구' },
        { name: '피겨', value: '피겨' },
        { name: '농구', value: '농구' },
        { name: '스피드스케이팅', value: '스피드스케이팅' },
      ],
      thumb: null,
      thumb_detail: null,
      starContent: {
        genre: '',
        name: '',
        major: '',
        description: '',
      },
    }
  },
  methods: {
    onChangeThumb(e) {
      this.thumb = e.target.files[0]
      console.log(this.thumb)
    },
    onMultyImageChange(e) {
      console.log(e.target.files)
      this.thumb_detail = e.target.files
      console.log(this.thumb_detail)
    },
    onDeleteMultyImageFile(idx) {
      const dataTransfer = new DataTransfer()

      let fileArray = Array.from(this.thumb_detail) //변수에 할당된 파일을 배열로 변환(FileList -> Array)
      fileArray.splice(idx, 1) //해당하는 index의 파일을 배열에서 제거
      fileArray.forEach((file) => {
        dataTransfer.items.add(file)
      }) //남은 배열을 dataTransfer로 처리(Array -> FileList)

      this.thumb_detail = dataTransfer.files
    },
    onChange(e) {
      const { name, value } = e
      this.$set(this.starContent, name, value)
      console.log(this.starContent)
    },
    async onSubmit() {
      if (this.starContent.genre == '') {
        window.alert('장르를 선택해주세요')
        return
      }
      if (this.starContent.name == '') {
        window.alert('이름을 입력해주세요.')
        return
      }
      if (this.starContent.major == '') {
        window.alert('전공을 입력해주세요.')
        return
      }
      if (this.starContent.description == '') {
        window.alert('선수 소개를 입력해주세요.')
        return
      }
      if (this.thumb == null) {
        window.alert('대표사진을 입력해주세요.')
        return
      }
      if (this.thumb_detail == null) {
        window.alert('추가사진들을 입력해주세요.')
        return
      }

      const formData = new FormData()
      formData.append('genre', this.starContent.genre)
      formData.append('name', this.starContent.name)
      formData.append('major', this.starContent.major)
      formData.append('description', this.starContent.description)
      formData.append('thumb', this.thumb)
      for (let i = 0; i < this.thumb_detail.length; i++) {
        if (this.thumb_detail[i] == null) {
          continue
        }
        formData.append('thumb_detail', this.thumb_detail[i])
      }

      const res = await api.postSportStar(formData)
      if (res.success) {
        window.alert('성공적으로 업로드되었습니다.')
        window.location.reload()
      }
      console.log(res)
      console.log('gogo')
    },
  },
}
</script>
