<template>
  <div>
    <!-- <div>
      <h2>종목</h2>
      <v-select :items="items" label="Solo field" solo></v-select>
    </div> -->
    <Select
      :label="true"
      labelHolder="클럽명"
      name="clubId"
      :items="items"
      style="margin-bottom: 75px"
      @onChange="changeClub"
    />

    <div style="display: flex; align-items: flex-end; margin-bottom: 75px">
      <div style="width: 100%; max-width: 882px; position: relative">
        <Input
          :label="true"
          labelHolder="기부자 이름"
          name="name"
          placeholder="텍스트입력"
          @onKeyUp="onSearchKeyWord"
        />
        <p style="position: absolute; top: 100%" v-if="searchResult">
          {{ searchResult }}
        </p>
      </div>
      <button @click="onSearch" style="margin-left: 20px" class="input-button">
        search
      </button>
    </div>
    <Input
      :label="true"
      labelHolder="작성일시"
      type="date"
      name="regdate"
      placeholder="텍스트입력"
      style="margin-bottom: 75px"
      @onKeyUp="onChange"
      max="9999-12-31"
    />
    <Input
      :label="true"
      labelHolder="기사제목"
      name="title"
      placeholder="텍스트입력"
      style="margin-bottom: 75px"
      @onKeyUp="onChange"
    />
    <!-- <Input
      :label="true"
      labelHolder="직책"
      name="belong"
      placeholder="텍스트입력"
      style="margin-bottom: 75px"
      @onKeyUp="onChange"
    /> -->
    <TextArea
      name="contents"
      minHeight="300px"
      :label="true"
      labelHolder="기사 내용"
      style="margin-bottom: 140px"
      @onKeyUp="onChange"
    />

    <MultyImageInput
      name="thumb_detail"
      :label="true"
      labelHolder="기사 사진"
      @onChange="onMultyImageChange"
      @remove="onDeleteMultyImageFile"
      style="margin-bottom: 75px"
    />

    <ButtonGroup @ok="onSubmit" />
  </div>
</template>
<script>
import Select from '../components/common/Select.vue'
import Input from '../components/common/Input.vue'

import TextArea from '../components/common/TextArea.vue'

import MultyImageInput from '../components/common/MultyImageInput.vue'
import ButtonGroup from '../components/ButtonGroup.vue'

import api from '../api'

export default {
  components: {
    Input,
    TextArea,
    Select,

    MultyImageInput,
    ButtonGroup,
  },
  data() {
    return {
      items: [
        { value: 1, name: 'Predident’s Honor Club ( 50억+ )' },
        { name: 'Platinum Honor Club ( 30억+ )', value: 2 },
        { value: 3, name: 'Gold Honor Club ( 10억+ )' },
        { value: 4, name: 'Rosegold Honor Club ( 5억+ )' },
        { value: 5, name: 'Whitegold Honor Club ( 3억+ )' },
        { value: 6, name: 'Silver Honor Club ( 1억+ )' },
        { value: 7, name: 'Partner Club ( 1천만원+ )' },
        { value: 8, name: '일반 기부자' },
      ],
      clubId: null,
      search: '',
      searchResult: '',
      thumb_detail: null,
      articleContent: {
        title: '',
        regdate: '',
        contents: '',
      },
      donor: null,
    }
  },
  methods: {
    onChange(e) {
      const { name, value } = e
      this.$set(this.articleContent, name, value)
      console.log(this.articleContent)
    },
    changeClub(e) {
      const { value } = e
      this.clubId = value
    },
    onSearchKeyWord(e) {
      const { value } = e
      console.log(e)
      this.search = value
    },
    onMultyImageChange(e) {
      console.log(e.target.files)
      this.thumb_detail = e.target.files
      console.log(this.thumb_detail)
    },
    onDeleteMultyImageFile(idx) {
      const dataTransfer = new DataTransfer()

      let fileArray = Array.from(this.thumb_detail) //변수에 할당된 파일을 배열로 변환(FileList -> Array)
      fileArray.splice(idx, 1) //해당하는 index의 파일을 배열에서 제거
      fileArray.forEach((file) => {
        dataTransfer.items.add(file)
      }) //남은 배열을 dataTransfer로 처리(Array -> FileList)

      this.thumb_detail = dataTransfer.files
    },
    async onSearch() {
      if (this.clubId === null) {
        window.alert('클럽을 선택해주세요')
        return
      }
      if (this.search === '') {
        window.alert('이름을 입력해주세요.')
        return
      }
      const res = await api.searchDonor({
        clubId: this.clubId,
        search: this.search,
      })
      console.log(res)
      if (res.success) {
        this.searchResult = '검색 완료'
        this.donor = res.data
      } else {
        this.searchResult =
          '일치하는 기부자가 없습니다. 등록한 기부자명을 정확히 입력해주세요.'
      }
    },
    async onSubmit() {
      console.log(this.thumb_detail.length)
      if (this.donor === null) {
        window.alert('기부자를 검색해주세요.')
        return
      }
      if (this.articleContent.title == '') {
        window.alert('제목을 입력해주세요.')
        return
      }
      if (this.articleContent.regdate == '') {
        window.alert('기사 등록일을 입력해주세요.')
        return
      }
      if (this.articleContent.contents == '') {
        window.alert('기사 내용을 입력해주세요.')
        return
      }
      if (this.thumb_detail == null) {
        window.alert('기사 사진들을 입력해주세요.')
        return
      }

      const formData = new FormData()
      formData.append('clubId', this.clubId)
      formData.append('name', this.donor.name)
      formData.append('donorId', this.donor.id)
      formData.append('title', this.articleContent.title)
      formData.append('regdate', this.articleContent.regdate)
      formData.append('contents', this.articleContent.contents)
      for (let i = 0; i < this.thumb_detail.length; i++) {
        if (i == 0) {
          console.log('init appended')
          formData.append('init', this.thumb_detail[i])
          continue
        }
        if (this.thumb_detail[i] == null) {
          continue
        }
        console.log('article appended')
        formData.append('article', this.thumb_detail[i])
      }
      console.log(formData)

      const res = await api.postArticle(formData)
      if (res.success) {
        window.alert('성공적으로 업로드되었습니다.')
        window.location.reload()
      }
      console.log(res)
      console.log('gogo')
    },
  },
}
</script>
